:root{
    --pdf-color:rgba(0,0,0,0.6);
    --pdf-theme: rgba(54, 68, 194, 0.993);
    --pdf-grey : rgb(250, 250, 250);
}

.pdf--container ::selection{
    background-color: rgba(0, 102, 255, 0.966);
    color: white;
}
.pdf--container{
    width: 210mm;
    min-height: 297mm;
    background-color: white;
    margin: 3em auto;
    padding: 3.3em;
    color: var(--pdf-color);
}
.pdf--brand{
    font-size: 1.6em;
    color:var(--pdf-theme);
    text-transform: uppercase;
    letter-spacing: 0.9;
}
hr{
    opacity: 0.4;
    margin: 1.2em 0;
}
address{
    white-space: pre-line;
}
text-right{
    text-align: right;
}
.table{
    width: 100%;
    margin: 2em 0;
    text-align: left;
    border: 1px solid lightgray;
    border-bottom: none;
    border-radius: 3px;
}
.table thead{
    background-color: var(--pdf-grey);
}
.add-btn{
    background-color: transparent;
    outline: 0;
    color: var(--bg-theme);
    font-size: 1em;
    border: none;
    font-weight: 500;
    cursor: pointer;
    transition: 0.1s;
}
tr{
    position: relative;
}
.table th, .table td{
    padding: 10px 6px;
    border-bottom: 1px solid lightgray;
}
.table th.pr-2{
    padding-right: 2em;
}
[contenteditable="true"]{
    position: relative;
    outline: 0;
    
}
[contenteditable="true"]:focus{
    border: 1px dashed black;
}
[contenteditable="true"]::after
{
    content: "\f303";
    display: block;
    position: absolute;
    right: -20px;
    top: calc(50% - 5px);
    font-family: "Fontawesome";
    font-size: 10px;

}
.font-13{
    font-size: 13px;
}
.text-white{
    color: white;
}

@media print{
    html{
    -webkit-print-color-adjust: exact !important;
    }
    nav{
        display: none;
    }
    .pdf--container{
        width: 100%;
    }
    [contenteditable="true"]:focus{
        border: none;
    }
    [contenteditable="true"]::after
    {
        content: unset;
        display: unset;
    
    }
    button{
        display: none;
    }
}
