@font-face {
  font-family: 'Fontawesome';
  src: url('./fonts/fontawesome.woff2');
}
::-webkit-scrollbar{
  width: 0;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  scroll-behavior: smooth;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

::selection{
  background-color: transparent;
}
a:active, a:visited{
  color: unset;
}
.p-2{
  padding: 2em;
}
.row-reverse{
  flex-direction: row-reverse;
}
.bg-white{
  background-color: white;
}
.font-bold{
  font-weight: bold;
}
.font-regular{
  font-weight: 500;
}
.text-right{
  text-align: right;
}
:root{
  --bg-theme: #df0269;
  --bg-light: rgb(247, 247, 247);
  --bg-yellow: #fdd835;
}
body{
  background-color: var(--bg-light);
}
input{
  outline: 0;
}
.bg-theme{
  background-color: var(--bg-theme);
}
.bg-yellow{
  background-color: var(--bg-yellow);
}
.py-3{
  padding: 10px;
}
nav{
  color: white;
}
.container{
  width: 100%;
  max-width: 1200px;
}
.container-mini{
  width: 100%;
  max-width: 700px;
}
.flex{
  display: flex;
}
.align-center{
  align-items: center;
}
.font-semi-bold{
  font-weight: 600;
}
.font-17{
  font-size: 17px;
}
.justify-between{
  justify-content: space-between;
}
.justify-evenly{
  justify-content: space-evenly;
}
.px-1{
  padding-left: 1em;padding-right: 1em;
}
.text-center{
  text-align: center;
}
.mx-auto{
  margin-left: auto;
  margin-right: auto;
}
.search-container{
  width: 70%;
  max-width: 350px;
  position: relative;
}
.navSearch{
  width: 100%;
  padding: 7px 30px 7px 10px;
  background-color: var(--bg-light);
  margin-left: 8px;
  border: none;
  caret-color: var(--bg-theme);
}
.search-container::after{
  font-family: 'Fontawesome';
  font-size: 15px;
  content: "\f002";
  color: var(--bg-theme);
  position: absolute;
  right: 3px;
  top: 8px;
}
.remove-button{
  position: absolute;
  height: 23px;
  width: 23px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: rgb(236, 3, 112);
  outline: 0;
  border: none;
  font-size: 12px;
  clip-path: polygon(0 0, 100% 10%, 100% 91%, 0% 100%);;
  right: -23px;
  top: calc(50% - 10px);
  opacity: 0;
  pointer-events: none;
}
.pdf--table_row:focus .remove-button{
  pointer-events: auto;
  opacity: 1;
  cursor: pointer;
}

.mt-4{
  margin-top: 2em ;
}
.mt-2{
  margin-top:1em;
}
.grid{
  width: 15%;
  flex-shrink: 0;
}
.block{
  display: block;
}
.grid img{
  width: 100%;
  cursor: pointer;
}
.wrap{
  flex-wrap: wrap;
}
.small{
  font-size: 11px;
}
.opacity-50{
  opacity: .5;
}
.mb-2{
  margin-bottom: 2em;
}
.text-grey{
  color: rgb(131, 131, 131);
}
.mb-1{
  margin-bottom: 1em;
}


@media only screen and (max-width: 900px)
{
  .grid{
    width: 28%;
  }
}
@media only screen and (max-width: 600px)
{
  .mb-md-0{
    margin-bottom: 0;
  }
  .grid{
    width: 46%;
  }
  .flex .w-50{
    width: 100%;
  }
}
.backdrop{
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0 ,0 ,0.1);
  top: 0;right: 0;left: 0;bottom: 0;
  transition: 0.2s;
  display: flex;
  opacity: 0;
  pointer-events: none;
}
.alert{
  position: relative;
  width: 400px;
  margin: auto;
  min-height: 180px;
  background-color: white;
  padding: 0.8em;
  display: flex;
  flex-direction: column;

}
.pre-line{
  white-space: pre-line;
}
.alert h3{
  color: rgb(109, 109, 109);
  font-weight: normal;
  font-size: 0.9em;
}
.cursor-pointer{
  cursor: pointer;
}
a.btn-theme, button.btn-theme{
  background-color: var(--bg-theme);
  outline: 0;
  border: 1px solid var(--bg-theme);
  color: white;
  padding: 0.5em 1.8em;
  display: inline-block;
  flex-grow: 0;
  width: fit-content;
}

.rounded{
  padding: 5px;
  border-radius: 50%;
  transition: 0.2s;
  cursor: pointer;
}
.rounded:hover{
  background-color: rgba(255, 255, 255, 0.3);
}
.mt-auto{
  margin-top: auto;
}
.ml-auto{
  margin-left: auto;
}
.ml-2{
  margin-left: 2em;
}
.input{
  display: block;
  padding: 5px 12px;
  outline: none;
  border-radius: 3px;
  transition: 0.09s;
  border: 1px solid var(--bg-theme);

}
.input:focus{
  border-width: 2px;
}
.w-50{
  width: 47%;
}
pr-2{
  padding-right: 2em;
}
.w-100{
  width: 100%;
}
.pdf--table_row:focus{
  outline: var(--bg-theme) solid 2px;
}
.context-menu{
  background-color: white;
  width: 300px;
  padding: 12px 18px;
  list-style: none;
  box-shadow: 0 0 20px rgba(0, 0 ,0 ,0.07);
  font-size: 12.7px;
  position: absolute;
  top: 0;
  z-index: 9;
  

}
.item-grid .grid{
  padding: 5px;
  border-radius: 3px;
  border: 2px solid transparent;
  transition: 0.09s;
}
.item-grid .grid a{
  color: black;
}
.item-grid .grid:hover{
  border: 2px solid var(--bg-theme);
  background-color: rgba(0, 0 ,0 ,0.06);
}
.context-menu li{
  margin: 10px 0;
  cursor: pointer;
}
.historyTable{
  width:100%;
  border-collapse: collapse;
}
.historyTable tr{
  border-bottom: 1px solid rgb(196, 196, 196);
  transition: 0.2s;
}
.historyTable tr:hover{
  background-color: white;
  box-shadow: 0 0 14px rgba(0, 0 ,0 ,0.07);
}
.historyTable img{
  width:30px;
  margin-right: 15px;
}
.historyTable td{
  padding: 17px 6px;
  font-size: 13px;
}
.historyTable h5{
  font-size: 13px;
  color: rgb(49, 49, 49);
}
.my-2{
  margin-top: 2em;
  margin-bottom: 2em;
}
