:root{
    --pdf-color:rgba(0,0,0,0.6);
    --pdf-theme: #8547e8;
    --pdf-grey : rgb(250, 250, 250);
}

.pdf--container-1 ::selection{
    background-color: rgba(0, 102, 255, 0.966);
    color: white;
}
.p-3em{
    padding: 3.3em;
}
.pdf--container-1{
    width: 210mm;
    min-height: 297mm;
    background-color: white;
    margin: 3em auto;
    color: var(--pdf-color);
    display: flex;
    flex-direction: column;
}
.pdf--bg--theme{
    background-color: var(--pdf-theme);
    color: white;
}
.pdf--bg--theme [contenteditable="true"]:focus{
    border-color: white;
}
.pdf--container-1 .pdf--brand{
    font-size: 1.7em;
    color:white;
    text-transform: uppercase;
    letter-spacing: 0.9;
}
hr{
    opacity: 0.4;
    margin: 1.2em 0;
}
address{
    white-space: pre-line;
}
text-right{
    text-align: right;
}
.pdf--theme-color{
    color: var(--pdf-theme);
}
.bg-light{
    background-color: rgb(0,0,0,0.02);
}
.fill{
    height: max-content;
}
.pdf--container-1 .table{
    width: 100%;
    margin: 2em 0;
    text-align: left;
    border: 1px solid lightgray;
    border-bottom: none;
    border-radius: 3px;
    font-size: 13px;
}
.pdf--container-1 .table thead{
    background-color: white;
}
.pdf--container-1 .table th{
    font-weight: 500;
}
.pdf--container-1 .add-btn{
    background-color: transparent;
    outline: 0;
    color: var(--pdf-theme);
    font-size: 1em;
    border: none;
    font-weight: 500;
    cursor: pointer;
    transition: 0.1s;
}
tr{
    position: relative;
}
.pdf--container-1 .table th,.pdf--container-1 .table td{
    padding: 10px 6px;
    border-bottom: 1px solid lightgray;
    border-top: 1px solid lightgray;
}
.pdf--container-1 .table th.pr-2{
    padding-right: 2em;
}
[contenteditable="true"]{
    position: relative;
    outline: 0;
    
}
[contenteditable="true"]:focus{
    border: 1px dashed black;
}
[contenteditable="true"]::after
{
    content: "\f303";
    display: block;
    position: absolute;
    right: -20px;
    top: calc(50% - 5px);
    font-family: "Fontawesome";
    font-size: 10px;

}
.font-13{
    font-size: 13px;
}
.text-white{
    color: white;
}

@media print{
    html{
    -webkit-print-color-adjust: exact !important;
    }
    nav{
        display: none;
    }
    body{
        padding: 0;
    }
    .pdf--container-1{
        padding: 1.2em;
        width: 100%;
        margin: 0 !important;
    }
    [contenteditable="true"]:focus{
        border: none;
    }
    [contenteditable="true"]::after
    {
        content: unset;
        display: unset;
    
    }
    button{
        display: none;
    }
}
